/* eslint-disable */
import React from 'react';
import PropTypes from 'prop-types';
import Media from 'react-media';
import Paper from '@material-ui/core/Paper';
import { withStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import ReBind from '../../assets/ReBind';
import Button from '@material-ui/core/Button';
import GroupAddIcon from '@material-ui/icons/GroupAdd';
import homePageImage from '../../assets/home-page.jpg';
/* eslint-enable */

const ScalableLandingView = ({ classes, scale }) => (
  <Paper className={classes.root}>
        <Typography
          style={{ fontSize: scale(styles.display1.fontSize) }}
          variant="display1"
          color="inherit"
          component="h1"
        >
          Rebind
        </Typography>
        <Typography
          style={{ fontSize: scale(styles.display2.fontSize) }}
          variant="display2"
          color="inherit"
          component="p"
        >
    is a project that brings the world closer together by making it simple to connect with anyone.
<br />This is achieved by empowering the users to present themselves to everyone with just one word.
<br />It is a cloud electronic contact book that is tailored to the natural way of human networking.
        </Typography>
        <Button
          style={{
            fontSize: scale(styles.button.fontSize),
            height: scale(styles.button.height),
            width: scale(styles.button.width)
          }}
          size="medium"
          variant="contained"
          color="primary"
          className={classes.button}
          onClick={() => console.log('clicked')}
        >
          <GroupAddIcon
            color="secondary"
            className={classes.icon}
            style={{
              fontSize: scale(styles.icon.fontSize),
            }}
          />
          Register
        </Button>
      </Paper>
);

ScalableLandingView.propTypes = {
  classes: PropTypes.object.isRequired,
  scale: PropTypes.func.isRequired
};

const LandingPageUI = ({ classes }) => (
  <div>
    <Media query={MEDIA_SCALE.SMALL.query} >
      <ScalableLandingView classes={classes} scale={MEDIA_SCALE.SMALL.scale} />
    </Media>
    <Media query={MEDIA_SCALE.MEDIUM.query}>
      <ScalableLandingView classes={classes} scale={MEDIA_SCALE.MEDIUM.scale} />
    </Media>
  </div>
);


const styles = {
  display1: {
    fontSize: 75
  },
  display2: {
    fontSize: 16
  },
  button: {
    marginTop: '5%',
    fontSize: 30,
    height: 60,
    width: 250
  },
  icon: {
    fontSize: 45,
    margin: '15px'
  },
  root: {
    color: ReBind.THEME.palette.primary.main,
    backgroundColor: ReBind.THEME.palette.primary.contrastText,
    paddingTop: '70px',
    height: '100vh',
  },
  card: {
    color: ReBind.THEME.palette.primary.contrastText,
    backgroundColor: ReBind.THEME.palette.primary.main,
    marginLeft: 100
  },
  media: {
    height: 140,
  }
};

/* order them from smallest to biggest */
const MEDIA_SCALE = {
  SMALL: {
    query: {
      maxWidth: 830,
    },
    scale: pixels => pixels
  },
  MEDIUM: {
    query: [{
      maxWidth: 1860,
      minWidth: 830,
    }],
    scale: pixels => `${pixels * 2}px`
  }
};

LandingPageUI.propTypes = {
  classes: PropTypes.object.isRequired
};

export default withStyles(styles)(LandingPageUI);
